<template>
  <div id="course">
    <head-top :title="courseInfo.CourseName||'课程'"/>
    <div class="bg">
      <div class="bg-title"></div>
      <div class="title">{{courseInfo['CHName']}}</div>
      <div class="line-info ">
        <span>老师：{{courseInfo['TeacherName']}}</span>
        <span>班级：{{courseInfo['ClassName']}}</span>
      </div>
      <div class="line-info bg_foot">
        <span>教室：{{courseInfo['ClassroomName'] || '无教室'}}</span>
        <span>
          时间：
          {{ courseInfo["CreateTime"] | formatDate(courseInfo["CreateTime"]) }}
        </span>
      </div>
    </div>
    <div class="content">
      <div class="item" v-for="items in courseDetails" :key="items.text" @click="goTo(items.path)">
        <div class="icon">
          <i class="iconfont" :class="items.icon"></i>
        </div>
        {{items.text}}
      </div>
    </div>

    <van-popup class="dialog" position="top" v-model="show">
      <div v-for="(item, i) in groupList" :key="i" class="ct" :class="{selected: selectIndex == i}"
           @click="selectChange(item ,i)">
        <div class="name-info">
          <div class="name">{{ (i + 1 ) + '、' + (item.groupName || '第' + (i + 1 ) + '组')}}</div>
          <div class="info" v-show="dialogTitle==='选择加入小组'">
            <span v-if="item.currentNum === item.groupMax">已满</span>
            <span v-else>{{item.currentNum + ' / '+ item.groupMax }}</span>
          </div>
        </div>
        <div class="xx">
          <i class="iconfont icon-finish" v-show="selectIndex==i"></i>
        </div>
      </div>
      <div class="pop_foot">
        {{ selectIndex > -1 ? '加入：' + ( groupList[selectIndex].groupName || '第' + (selectIndex + 1 ) + '组') :
        dialogTitle}}
        <van-button class="pop_foot_btn" type="primary" plain size="small" @click="confirmDialog()">
          加入
        </van-button>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { GroupType } from "@/utils/enum";
import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "",
  components: { [Dialog.Component.name]: Dialog.Component },
  props: {},
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // vm.$gatewayHttp.gateway.linkTest().then(res => {
      //   vm.linkState = 1;
      // }).catch(err => {
      //   vm.linkState = 0;
      //   Dialog.alert({
      //     title: "警告",
      //     message: "找不到当前课程服务！即将退出"
      //   }).then(() => {
      //     // on confirm
      //     next("/home-course");
      //   });
      // });
    });
  },
  beforeRouteLeave (to, from, next) {
    let haveCourse = this.$store.getters.courseID && to.path === "/home-course";
    let isCourseNow =
      this.$store.getters.nowCourse &&
      this.$store.getters.courseID !== this.$store.getters.nowCourse["CIID"];
    if ((haveCourse && isCourseNow) || !this.linkState) {
      next();
    }

    // (!to.meta.limits || to.meta.limits.indexOf(LimitType.inCourse) === -1) &&
    if (haveCourse && window.GatewayHub && window.GatewayHub.connection.state === 1) {
      Dialog.confirm({
        title: "警告",
        message: "是否强制退出当前课程？"
      }).then(() => {
        // on confirm
        next();
      }).catch(() => {
        // on cancel
        next(false);
      });
    } else {
      next();
    }
  },
  data () {
    return {
      linkState: 1,
      selectIndex: -1,
      groupList: [],
      courseInfo: this.$store.getters.course,
      courseDetails: [
        {
          path: "/signIN",
          icon: "icon-qiandao",
          text: "签到"
        },
        {
          path: "/groupInfo",
          icon: "icon-renmai",
          text: "我的小组"
        },
        {
          path: "/groupDiscussion",
          icon: "icon-taolun",
          text: "小组讨论"
        },
        {
          path: "/teacherscreen",
          icon: "icon-pingmu-",
          text: "老师屏幕"
        },
        {
          path: "/quiz",
          icon: "icon-weibiaoti-",
          text: "随堂测验"
        },
        {
          path: "/question",
          icon: "icon-tiwen2",
          text: "提问"
        },
        {
          path: "/touping",
          icon: "icon-touping",
          text: "投屏"
        },
        {
          path: "/note",
          icon: "icon-edu-biji",
          text: "笔记"
        },
        {
          path: "/blackboardWriting",
          icon: "icon-banshubianjiqi-dianji",
          text: "板书"
        },
        {
          path: "/groupNote",
          icon: "icon-groupnote",
          text: "小组笔记"
        },
        {
          path: "/liveLesson",
          icon: "icon-weike",
          text: "课堂实况"
        },
        {
          path: "/courseware",
          icon: "icon-kejian",
          text: "课件"
        }
        // {
        //   path: "/start",
        //   icon: "icon-kejian",
        //   text: "多点投屏"
        // },
        // {
        //   path: "/close",
        //   icon: "icon-kejian",
        //   text: "关闭多点"
        // }
      ],
      show: false,
      dialogTitle: "",
      cifmBtnText: ""
    };
  },
  created () {
    // this.$lessonSignalR.startGatewaySignalR();
    // sendFreshGroup(false);
  },

  mounted () {
    let colors = [
      "#00CBAB",
      "#3AA0E0",
      "#E38653",
      "#F95F5F",
      "#A36EE5",
      "#E38653",
      "#3AA0E0",
      "#00CBAB",
      "#A36EE5",
      "#3AA0E0",
      "#E38653",
      // "#A36EE5",
      "#00CBAB"
    ];
    let tempArr = [];
    for (let index = 0; index < this.courseDetails.length; index++) {
      const element = this.courseDetails[index];
      let Jqicon = "." + element.icon;
      tempArr.push(Jqicon);
    }
    for (let index = 0; index < colors.length; index++) {
      $(tempArr[index]).css("color", colors[index]);
    }
  },
  methods: {
    openGroupDialog () {
      if (!this.$store.getters.groupID && this.$store.getters.groupType + "" === GroupType.Free) {
        this.$router.push("/joinGroup");
        // this.getGroupDataList(false);
      }
    },
    getGroupDataList (toPath) {
      this.$store.dispatch("GetGroupByUser").then(res => {
        if (!res.Result && !res.IsGrouping) {
          this.$toast("当前尚无分组~");
        } else if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            this.groupList = [];
            res.Datas.forEach(item => {
              let groupItem = {
                gatewayID: item.Key["GatewayID"],
                groupID: item.Key["GroupID"],
                groupName: item.Key["GroupName"],
                groupMax: item.Key["GroupNumber"],
                groupingID: item.Key["GroupingID"],
                studentID: item.Key["StudentID"],
                currentNum: item.Total
              };
              this.groupList.push(groupItem);
            });

            this.$router.push("/joinGroup");
          } else if (res.Data) {
            if (this.$store.getters.groupID) {
              this.$router.push(toPath);
            }
          }
          this.groupList = [];
        }
      }).catch(() => {
        this.groupList = [];
      });
    },

    getSignWay () {
      this.$mainHttp.sign.dataList({
        ciID: this.$store.getters.courseID,
        studentID: this.$store.getters.studentID
      }).then(res => {
        if (res.SignModel) {
          this.$router.push("/signIN/" + res.SignModel + "/" + (res.Result ? "1" : "0"));
        } else {
          this.$toast("获取签到状态失败~");
        }
      }).catch(err => {
        this.$toast("获取签到状态异常~");
      });
    },
    selectChange (item, i) {
      if (item.currentNum === item.groupMax) return;
      this.selectIndex = i;
    },
    confirmDialog () {
      // console.log(dialogName, "名称");
      if (this.selectIndex === -1) {
        this.$toast("请选择小组");
        return;
      }
      if (this.dialogTitle === "选择加入小组") {
        this.joinGroup();
      } else if (this.dialogTitle === "投屏选择") {
        this.$router.push("/quiz");
      }
    },
    joinGroup () {
      let groupInfo = this.groupList[this.selectIndex];
      this.$store.dispatch("JoinGroup", groupInfo.groupID, groupInfo).then(res => {
        if (res.Result) {
          this.$toast("加入成功~");
          // this.$router.push("/groupDiscussion");
          this.getGroupDataList("/groupInfo"); //更新小组信息
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    // onChange (picker, value, index) {
    //   this.$toast(`当前值：${value}, 当前索引：${index}`);
    // },
    // goBack () {
    //   this.$router.go(-1);
    // },
    goTo (path) {
      if (path === "/groupDiscussion" || path === "/groupInfo") {
        this.getGroupDataList(path);
      } else if (path === "/signIN") {
        this.getSignWay();
      } else if (path === "/teacherscreen") {
        // 开启老师屏幕
        sendToAPP(FuncType.broadcastStart, "10").then(res => {
          // if (!res) this.$toast("请求老师屏幕失败~");
        });
      } else if (path === "/touping") {
        this.goToToupin();
        // } else if (path === "/start") {
        //   let value = prompt("请输入投屏地址：", "192.168.0.28,192.168.0.51");
        //   if (!value) return;
        //   // 开启多点投屏
        //   sendToAPP(FuncType.throwScreenStart, value).then(res => {
        //     if (!res) this.$toast("多点投屏开启失败~");
        //   });
        // } else if (path === "/close") {
        //   let value = prompt("请输入关闭的投屏地址", "-1");
        //   if (!value) return;
        //   // 关闭多点投屏
        //   sendToAPP(FuncType.throwScreenStop, value).then(res => {
        //     if (!res) this.$toast("多点投屏关闭失败~");
        //   });
      } else {
        this.$router.push(path);
      }
    },
    goTOxiaoz () {
      this.$router.push("/groupDiscussion");
    },
    goTOQuiz () {
      this.$router.push("/quiz");
    },
    goToToupin () {
      this.$store.dispatch("GetGroupByUser").then(() => {
        // 开启投屏
        sendToAPP(FuncType.throwScreenOpen, "").then(res => {
          // if (!res) this.$toast("请求投屏失败~");
        });
      }).catch(() => {
        // 开启投屏
        sendToAPP(FuncType.throwScreenOpen, "").then(res => {
          // if (!res) this.$toast("请求投屏失败~");
        });
      });
    }
  },
  watch: {
    "$store.getters.groupID" (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.openGroupDialog();
      }
    },
    "$store.getters.groupType" (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.openGroupDialog();
      }
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../styles/common/base.less");

#course {
  background: #ebf0ef;
  width: 100%;
  height: 100%;

  .bg {
    overflow: hidden;
    background: url("../../assets/bg_kecheng.png") 100% 100% no-repeat;
    width: 100vw;
    min-height: 30vw;
    color: rgba(255, 255, 255, 1);

    span {
      display: inline-block;
    }

    background-size: cover;

    .title {
      font-size: @font-title;
      font-family: MicrosoftYaHei;
      margin: 4vw 0;
    }

    .line-info {
      width: 100%;
      margin-bottom: 6px;

      span {
        font-size: @font-info;
        display: inline-block;
        width: 45vw;
        text-align: left;
        &:first-child{
          margin-left: 6vw;
        }
      }
    }

    .bg_foot {
      padding-bottom: 4vw;
    }
  }

  .content {
    text-align: left;
    box-sizing: border-box;
    width: 100%;
    height: 52vh;
    background: white;

    .item {
      text-align: center;
      font-size: @font-title;
      width: 24.7vw;
      display: inline-block;
      border: 1px solid rgba(221, 221, 221, 1);
      border-left: none;
      border-top: none;
      height: 12vh;
      padding-top: 5vh;

      &:nth-child(9) {
        border-bottom: none;
      }

      &:nth-child(10) {
        border-bottom: none;
      }

      &:nth-child(11) {
        border-bottom: none;
      }

      &:nth-child(12) {
        border-bottom: none;
      }

      &:nth-child(4n + 0) {
        border-right: none;
      }

      .icon {
        .iconfont {
          margin-bottom: 10px;
          font-size: @font-title * 1.8;
          margin-bottom: 10px;
        }

        .icon-qiandao {
          color: #00cbab;
        }

        .icon-taolun {
          color: blue;
        }
      }
    }
  }

  .dialog {
    font-size: @font-info;
    margin-top: 44px;

    .van-dialog__content {
      margin-bottom: 4vw;
    }

    .van-dialog__header {
      border-bottom: 1px solid rgba(153, 153, 153, 1);
      box-sizing: border-box;
      padding-bottom: 25px;
      margin-bottom: 25px;
      font-size: @font-title * 1.3;
    }

    .selected {
      font-weight: bold;
      color: #07c160;
    }

    .ct {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      margin: 1vh 5vw;
      font-size: @font-sub-title;
      line-height: 2;
      border-bottom: 1px solid #f3f3f3;

      .name-info {
        width: 76vw;
        display: flex;
        justify-content: space-between;

        .name {
          margin-right: 20vw;
        }
      }

      .xx {
        width: 8vw;
        // height: 20px;
      }

      .icon-finish {
        font-size: @font-sub-title;
      }
    }
  }

  .pop_foot {
    text-align: left;
    margin: 0 5vw;
    padding: 2vh 0;
    border-top: 1px solid #333;
    font-size: @font-sub-title;
    line-height: 30px;

    .pop_foot_btn {
      float: right;
    }
  }
}
</style>



